@import url(https://fonts.googleapis.com/css?family=Rock+Salt&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.name {
	font-family: 'Rock Salt', cursive;
	font-size: 22px;
	color: white;
	margin-left: 20px;
	margin-top: 5px;
}

.text {
	font-size: 19px;
	text-align: center;
	background-color: #2094F9;
}

.margin-btn{
	margin-right: 20px;
}

a.nav-link {
	color: white;
	margin-right: 50px;
}
header {
	margin: 0 auto;
	text-align: center;
	padding-top: 100px;
}

header h1 {
	margin-left: 1em;
	margin-right: 1em;
	font-size: 3.5rem;
	color: white;
	background-color: rgb(135, 206, 250,0.5);
}

header p {
	font-size: 1.75rem;
	font-weight: 700;
	font-style: italic;
	color: black;
	font-family: 'Poppins', sans-serif;
	background-color: rgb(135, 206, 250,0.5);
	margin-left: 2em;
	margin-right: 2em;

}

@media (min-width: 300px) and (max-width: 480px) {
	header {
		padding-top: 80px;
	}

	header h1{
		font-size: 1.9rem;
	}
	header p{
		font-size: 1.1rem;
		margin-bottom: 3rem;
		color: #034AA8;
	}

}

@media (min-width: 481px) and (max-width: 767px) {
	header {
		padding-top: 100px;
	}

	header h1{
		font-size: 2.35rem;
	}
	header p{
		font-size: 1rem;
		margin-bottom: 3rem;
		color: #034AA8;
	}
  
}

.container{
	max-width: 100%
}

.aboutBorder{
	background-color: #BBDEFB;
	height: 450px;
	margin: 0;
	margin-right: 0;
	padding: 0;
}

 .about {
 	font-size: 19px;
 	text-align: justify;
 	font-family: 'Montserrat', sans-serif;
 }

.about p {
 	color: #5F6366;
 	font-family: 'Montserrat', sans-serif;
 }

 .image {
	width: 380px;
	height: 420px;
	margin-left: auto;
 	margin-right: auto;
}

h1 {
	padding-bottom: 0px;
}

 @media (min-width: 300px) and (max-width: 355px) {
 	
 	.about {
 		padding-top: 0px;
 		margin-left: 50px;
 	}
 	
 	.about p {
 		font-size: 11px;
 		text-align: center;
 		margin-right: 48px;

 	}

 	h1 {
 		margin-left: 68px;
 		color: black;
 		padding-bottom: 0px;
 	}

 	.image {
 		width: 150px;
 		height: 150px;
 		margin-left: 30%;
 		margin-top: 35px;

 	}
 }

 @media (min-width: 356px) and (max-width: 767px) {

 	.aboutBorder {
 			height: 420px;
 			width: 100%;
 
 	}

 	.about p {
 		font-size: 10.5px;
 		margin-left: 30%;
 	}

 	.image {
 		width: 165px;
 		height: 165px;
 		margin-top: 30px; 
 		margin-left: auto;
 		margin-right: auto;
 	}
  
}

@media (min-width: 768px) and (max-width: 1024px) {

	.about p {
		font-size: 16px;
		margin-right: 50px;
	}

	.image {
		height: 250px;
		width: 250px;
		margin-top: 100px;
		margin-left: 50px;
	}
}

@media (min-width: 1025px)  {

	.about p {
  	font-size: 17px;
  	}

  	.aboutBorder {
  		height: 500px;
  	}

  	.image {
  		width: 250px;
  		height: 250px;
  		margin-top: 90px;
  		margin-left: 50px;
  	}
 
}
.carousel-size {
	width: 500px;
	height: 500px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}

.image-fluid{
	height: 0px
}

.carousel-caption h4 {
		font-size: 25px;
		text-transform: uppercase;
		letter-spacing: 2px;
		color: black;
	}

.paws-color {
	color: white;
	background-color: rgb(13,122,247, 0.7);
}

.seafood-color {
	color: black;
	background-color: rgb(238,283,293, 0.9);
}

.pokemon-color {
	color: black;
	background-color: rgb(238,283,293, 0.8);
}

.text-margin {
	margin-left: 80px;
}

h1 {
	margin-left: auto;
	margin-right: auto;
	padding-bottom: 15%;
}


@media (min-width: 300px) and (max-width: 480px) {
	.carousel-size {
		width: 300px;
		height: 300px;
	}

	.carousel-item {
		height: 30vh;
		text-align:center;
	}

	.carousel-caption{
		padding-bottom: 0px;
	}

	.carousel-caption h4 {
		font-size: 15px;
	}

	.carousel-caption p {
		text-align: center;
		font-size: 11px;
	}

	.carousel-indicators li {
		 width: 7px;
		 height: 7px;
		 border-radius: 100%;
		 
		}

	.img-position {
		margin-right: auto;
		margin-right: auto;
		margin: 0;
	}

	.img-carousel {
		margin-top: 0;
		margin-left: 0;
		margin-right: 0;
	}

}

.content {
	padding-top: 100px;
 	font-size: 19px;
 }

 .paddingBorder {
 	padding: 0;
 	margin: 0;
 }


 p, h1 {
 	font-family: 'Montserrat', sans-serif;
 	font-size: 19px;
 	margin-top: 20px;
 }

 #projects {
 	min-height: 280px;
 }
.skillsBoarder {
	background-color: #90CAF9;
	height: 450px;
}

@media (min-width: 300px) and (max-width: 480px) {
  .skillsBoarder {
  	height: 730px;
  }

  .text {
 	font-size: 12px;
 }

 .title-size {
 	padding-top: 10%;
 	font-size: 20px;
 } 

 ul li {
 	font-size: 10px;
 }

 
  
}
.contactBorder{
	background-color: white;
	height: 200px;
}

 .about {
 	font-size: 19px;
 	text-align: justify;
 	padding-right: 90px;
 	color: white;
 }

 .contact {
 	font-size: 40px;
 }

 h1 {
 	padding-bottom: 0px;
 }

 @media (min-width: 300px) and (max-width: 480px) {
 	.contact {
 		font-size: 20px;
 	}

 	.content {
 		font-size: 12px;
 		padding-top: 0;
 	}

 	.logo-git {
 		width: 30px ;
 		height: 30px;
 	}

 	.logo-link {
 		width: 37px;
 		height: 37px;
 	}
 }

.app, body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	box-sizing: border-box;
	margin-right: auto;
	margin-left: auto;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", 
	monospace;
}



